.theme-selection {    
    display: table-row;    
    height: 100%;
    width: 100%;
    text-align:center;

    &__title {
        font-family: @kievitregular;
        font-size: 24px;
        line-height: 36px;
    }

    &__options {
        margin-top: 30px;

        &__light {
            display: table-cell;
            width: 50%;
            padding-right: 26px;
            text-align: right;
            vertical-align: top;
        }

        &__dark {
            display: table-cell;
            width: 50%;
            padding-left: 26px;
            text-align: left;
            vertical-align: top;
        }

        &__image {
            display: table-cell;
            padding: 2px;
        }

        &__radio {
            font-family: @kievitregular;
            padding-top: 8px;
            padding-left: 2px;
            font-size: 14px;
            line-height: 20px;
            text-align: left;

            &__text {
                display:inline;
                padding-left: 4px;
                vertical-align: top;
                letter-spacing: 0.3px;
            }

            &__subtext {
                padding-left: 20px;
                font-style: italic;
                margin-top: -5px;
                font-size: 12px;
            }
        }

        &__selected {
            box-sizing: content-box;
            border: 2px solid #49BC13;
            padding: 0px;
            display: inline-block;
        }
    }

    &__description {
        margin-top: 20px;
        font-family: @kievitregular;
        font-size: 14px;
        line-height: 20px;
        padding-left: 26px;
        padding-right: 26px;
        letter-spacing: 0.3px;
    }

    &__cta2 {
        text-decoration: underline;
        margin-top: 14px;
        font-family: @kievitregular;
        font-size: 16px;
        line-height: 22px;
        letter-spacing: 0.3px;
    }
}