.keyframes(@name; @arguments) {
	@-moz-keyframes @name { @arguments(); }
	@-webkit-keyframes @name { @arguments(); }
	@keyframes @name { @arguments(); }
}

.animation(@arguments) {
	-webkit-animation: @arguments;
	-moz-animation: @arguments;
	animation: @arguments;
}


@dropdown-height: 50px;

.regions {
	position:relative;
	border-bottom: 1.5px solid @border-color;
    width:100%;

	&__dropdown.dropdown--slim {
		width: auto;
		min-width: 150px;
		display: inline-block;
	}

	&__settingSelection {
		display: table-cell;
		height: 100%;
		vertical-align: middle;
		
		&__group {
			display:table;
			height: 54px;
			width: 154px;
			float: right;
		}
	}
}

.dropdown {
	cursor: pointer;
    display:table-cell;
    vertical-align:middle;
	.prefix(pointer-events, all);
	.transition(width 0.5s ease-in-out);
	* {
		.prefix(user-select, none);
		cursor: pointer;
	}

	&__icon {
		cursor: pointer;
		float: left;
		width: 32px;
		padding-top: 0px;
        display: table-cell;
        vertical-align: middle;
	}

	&__text {
		.fs-18();
		padding-left:8px;
        padding-top:0px;
        padding-bottom:0px;
		cursor: pointer;
		width: 140px;
        display:table-cell;
        vertical-align: middle;
	}

	&__popup {
		opacity: 0;
		display: none;
		position: absolute;
		width:205px;
		filter: alpha(opacity=0);
		max-height:315px;
		
		border: 1px solid @border-color;
		border-radius: 4px;
		background-color: white;
		.transition(opacity 0.15s ease-in-out, bottom 0.35s ease-in-out;);
		.ie8 &, .ie9 & {
			display: none;
		}
		padding-bottom:5px;
		padding-top:5px;

		overflow-x: hidden;
    	overflow-y: auto;
	}

	&--opened &__popup {
		z-index:1000;
		margin-left:-35px;
		margin-top:40px;
		
		opacity: 1;
		display: inline-block;
		filter: alpha(opacity=100);
		.prefix(pointer-events, all);

		.ie8 &, .ie9 & {
			display: inline-block;
		}
	}

	&__option {
		float: left;
		width: 100%;
		height: 35px;
		padding-left: 13px;
        display: table;
		color: @secondary-text-color;
		.transition(background-color 0.25s ease-in-out;);
		&:hover {
			background-color: lighten(@border-color, 6%);
		}
	}
}

.location {
	
	&__current {
		float: right;
		width:150px;
        height:50px;
        padding: 0px;
		color: @secondary-text-color;
	}

	&__icon {
		cursor: pointer;
        display: table-cell;
        vertical-align:middle;
	}

	&__text {
		.fs-18();
        max-width: 140px;
        position:relative;
		padding-right:3px;
		padding-left:5px;
        padding-top: 1px;
        padding-bottom:1px;
		cursor: pointer;
        display: table-cell;
        vertical-align:middle;
        word-wrap: break-word;
	}
	
	&__caret {
		width: 20px;
		text-align: right;
		color: @secondary-text-color;
		cursor: pointer;
		i {
			width: 100%;
			font-size: 14px;
			margin-top: 5px;
		}
        display: table-cell;
        vertical-align:middle;
	}

	&--slim &__caret {
		display: none;
	}

	&--notselectable &__text
	{
		cursor: not-allowed;	
	}

	&--notselectable &__icon
	{
		cursor: not-allowed;	
	}
}

.regions-flag {
	width: 25px;

	padding-right: 10px;
	display:table-cell;
	vertical-align:middle;

	.proRegion &{
		cursor: default;		
	}
}

.regions-pro {
	text-align: right;
	padding-right: 20px;
	display:table-cell;
	vertical-align: middle;
}

.regions-radiobutton {
    padding-left: 15px;
    padding-right: 15px;
    display: table-cell;
	vertical-align: middle;
	width: 46px;
}