.diagnostics {
    display: table;
    text-align: center;
    width:100%;
    line-height: 25px; 
    font-size: 18px;

    &__text {
        display: inline-block;
        width:80%;
        max-width: 535px;    
        padding-top: 20px;
    }

    &__footer {
        display: table-row;
        vertical-align: bottom;
        width: 100%;
        height: 109px;
        
        &__sendContainer {
            display: inline-block;
            width: 100%;
        }

        &__dontSend {
            margin-top: 20px;
            display: inline-block;
            cursor: pointer;
            font-size: 14px;
            color: #6F8299;
        }
    }
}