@color-settings-unchecked:  #BAC4C9;

.radiobutton {
  background: #fff;

  position: relative;
  display: inline-block;
  box-sizing: content-box;
  overflow: visible;
  width: 16px;
  height: 16px;
  padding: 0px;
  margin: 0px;
  border-radius: 16px;
  cursor: pointer;
  box-shadow: rgb(223, 223, 223) 0px 0px 0px 0px inset;
  transition: 0.3s ease-out all;
  -webkit-transition: 0.3s ease-out all;
  top: -1px;
  font-size:8px;
}

/*adding a wide width for larger switch text*/
.radiobutton.wide {
  width:80px;
}

.radiobutton.checked small:before {
   font-family: ionicons; 
   content: "\f383";//ion-android-done
} 



.radiobutton small {
  background: #fff;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  position: absolute;
  text-align: center;
  top: 0px;
  left: 0px;
  padding: 3px 0px;
  font-size:9px;
  border: 1px solid #A7B3B6;
}

.radiobutton.checked small {
  left: 0px;
  background: @primary-text-color !important;
  padding: 4px 5px;
  color: white;
  border: 0px solid #A7B3B6;
}

/*wider switch text moves small further to the right*/
.radiobutton.wide.checked small {
  left:40px;
}
/*styles for switch-text*/
.radiobutton .switch-text {
 // font-family:Arial, Helvetica, sans-serif;
  //font-size:23px;
}

.radiobutton .off {
  display:block;
  position: absolute;
  right: 10%;
  top: 25%;
  z-index: 0;
  color:#A9A9A9;
}

.radiobutton .on {
  display:none;
   z-index: 0;
  color:#fff;
  position: absolute;
  top: 25%;
  left: 9%;
}

.radiobutton.checked .off {
  display:none;
}

.radiobutton.checked .on {
  display:block;
}

.radiobutton.disabled {
  opacity: .50;
  cursor: not-allowed;
}
.showDiv
{
  visibility:visible !important;
}
