body {
	margin: 0;
	border-radius: 0;
	font-size:0px; // avoid spaces between div (at least on Mac) 
}

.vpn-console {
	width: 100%;
	height: 100%;
}

.vpn-console textarea {
	border: 1px solid @border-color;
	width: 100%;
	height: 100%;
	outline: none;
	padding: 4px;
	font-size: 10px;
	color: #333;
	font-family: courier;
}