.keyframes(@name; @arguments) {
	@-moz-keyframes @name { @arguments(); }
	@-webkit-keyframes @name { @arguments(); }
	@keyframes @name { @arguments(); }
}

.animation(@arguments) {
	-webkit-animation: @arguments;
	-moz-animation: @arguments;
	animation: @arguments;
}


@dropdown-height: 50px;

.location {
    text-align: center;
	height:23%;
    display: table;
    width: 100%;
	
    .vpn-showRegisterOption & {
        padding-top: 2px;
        height: 110px;
    }

    hr {
        display: block;
        margin-bottom: 44px;
        width: 75px;
        margin-left: auto;
        margin-right: auto;

        .license-is-expiring & {
            margin-bottom: 10px;
        }
    }

    .vpn-connected & hr {
        margin-top: -1px;
    }


    &__privacy_message {
        .fs-22();
        line-height: 24px;
        padding: 0 20px;
        font-weight: normal;
    }

    &__connecting_location {
        .fs-18();
        line-height: 20px;
        margin-top: 6px;
        padding: 0 20px;
        font-weight: normal;
        display: none;
        .vpn-connecting & {
            display: block;
        }
        .vpn-disconnecting & {
            display: block;
        }
}

    &__location_message {
        margin-top: 9px;
        padding: 0 5px;
    }

    &__license_expiration {
        .fs-18();
        display: none;

        .license-is-expiring & {
            display: block;
        }
    }

    .renew-button {
        margin-top: 9px;
        width: 100%;
        height: 30px;
        text-align: center;
        color: white;
    }

    .renew-button-circle {
        padding-left: 15px;
        padding-right: 15px;
        height: 100%;
        border-radius: 25px;
        overflow: hidden;
        background: #FFB400;
        padding-top: 7px;
        display: inline-block;
    }

    &__location_message, &__location_message a {
        .fs-18();
        line-height: 13px;
    }

    .vpn-disconnecting &__location_message {
        display: none;
    }

    .vpn-connecting &__location_message {
        display: none;
    }

    &__connection_message {
        .fs-20();
        line-height: 24px;
        margin-top: 16px;
        padding: 0 50px;
    }
}

