.puchasePage {
    display: table;
    height: 434px;        
    width: 100%;

    .uwp-app & {
        height: 100%;        
    }
}

.purchase {
    text-align: center;
    width: 100%;
    display: table-row;
    height: auto;
    background: #F3F5F6;

    &__container {
        display: table-cell;
        vertical-align: middle;
        width: 100%;
    }
    &__title {
        display: table;
        width: 100%;
        height: 43px;
        margin-top: 15px;
        span {
            font-size: 34px;            
            display: table-cell;
            vertical-align: middle;
        }
    }
    &__offers {
        margin-top: 15px;
        margin-bottom: 15px;
        display: table;
        width: 100%;
    }
    &__registerFirst {
        font-size: 18px;
        width: 55%;
        display: table-cell;
        vertical-align: middle;
        &__box {
            max-width: 510px;
            width: 100%;
            float: left;
            span {
                padding: 10px;
                display: block;
            }
        }
    }
    &__option {
        display: table-cell;
        &__box {
            display: table;
            border-collapse: separate;
            border-spacing: 0px;
            width: 100%;
            height: 100%;
            &__raw {
                display: table-row;
            }
            &__text {
                font-size: 18px;
                height: 25px;
                display: table-cell;
                vertical-align: middle;
            }
            &__container {
                display: table-cell;
                vertical-align: middle;
                padding-bottom: 5px;
            }
        }
        &__leftbox {
            display: table;
            border-collapse: separate;
            border-spacing: 0px;
            width: 100%;
            height: 100%;
            float: right;
            max-width: 330px;
        }
    }
    &__description {
        width: 100%;
        font-size: 18px;
        display: table;
        &__box {
            display: table-cell;
            vertical-align: middle;
            width: 100%;
        }
        &__text {
            width: 100%;
            display: inline-block;

            .uwp-app & {
                margin-bottom: 45px;
            }
        }
        &__offer {
            display: inline-block;

            &__box {
                width: 100%;
            }
            &__info {
                display: table;
                &__period {
                    display: table-cell;
                    float: left;
                    text-align: left;
                    margin-left: 12px;
                    max-width: 190px;
                    &__trial {
                        span {
                            color: #23be04;
                        }
                    }
                }
                &__price {
                    margin-left: 4px;
                    display: table-cell;
                    float: left;
                    color: #23BE04;
                }
            }
        }
    }
}

.purchaseButtons {
    text-align: center;
    display: table-cell;
    width: 100%;
    height: 79px;
    
    .uwp-app & {
        vertical-align: bottom;
    }

    &__buyButton {
        height: 50px;
        display: inline-block;
        width: 310px;
        text-align: center;
        &__box {
            width: 100%;
            display: table;
        }
    }
    &__otherOptions {
        width: 310px;
        display: inline-block;
        margin-top: 10px;
        margin-bottom: 10px;
        &__element {
            font-size: 14px;
            width: 100%;
            &__notNow_default {
                color: @color-disabled;
                text-align: right;
                float: right;
                cursor: pointer;
            }
            &__notNow_centered {
                color: @color-disabled;
                cursor: pointer;
                display: inline-block;
            }
            &__restore_default {
                color: #2B88F6;
                text-align: left;
                float: left;
                cursor: pointer;
            }
            &__restore_hidden {
                display: none;
            }
        }
    }
}

.subscriptionTerms {
    font-size: 14px;
    vertical-align: bottom;
    text-align: center;
    display: table-row;
    width: 100%;
    background-color: #F3F5F6;

    &__collapsed {
        height: 30px;
    }

    &__expanded {
        height: 370px;        
    }

    &__title {
        margin-top: 7px;
        span {
            cursor: pointer;
        }
    }

    &__description {
        margin-left: 40px;
        margin-right: 40px;
        margin-top: 10px;
        text-align: justify;
        text-justify: inter-word;
        line-height: 19px; 
    }

    &__button {
        margin-top: 10px;
        color: #2B88F6;
        span {
            cursor: pointer;
        }
    }
}

.button {
    color: white;
    cursor: pointer;
    border: 1px;
    background-color: #23BE04;
    border-radius: 2px;
    font-size: 22px;
    padding: 14px;
    &__disabled {
        background-color: transparent;
        color: #7E93A7;
        border: 1px solid white;
        padding: 14px;
        font-size: 22px;
        cursor: default;
    }
}
