.settings-view {    
    display: table-row;    
    height: 100%;
    width: 100%;
}

.settings {
    border-bottom: 1px solid @border-color;
    display: table;
    width: 100%;
    position: relative;
    background-color: white;
    min-height: 67px;

    &__common {
        text-align: left;
        cursor: default;
        display: inline-block;
        .prefix(pointer-events, all);
    }

    &__left {
        display: table-cell;
        vertical-align: middle;
        text-align: left;
        white-space: nowrap;
    }

    &__header {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        font-size: 25px;
        width: 100%;

        &__fill div{
            opacity: 0;
            pointer-events: none;
        }
    }

	&__quickSearch {
        font-size: 18px;
        display: table-row;
        width: 100%;
        text-align: center;

        &__searchRectangle{
            box-sizing: border-box;
            position: relative;
            width: 90%;
            max-width: 535px;
            display: inline-block;
            height: 35px;
            border: none;
        }

        &__leftIcon{
            position:absolute;
            left:10px;
            text-align: left;
            top:7px;
            font-size: 20px;
        }

		&__textBox{
                border: 1px solid @border-color;
                width: 100%;
                height: 33px;
                padding-left: 35px;
                font-size: 18px;
                color: @secondary-text-color;
		}
	}

    &__setting-back {
        display: inline-block;
        font-size: 15px;
        text-align: left;
        padding-left: 4px;
        cursor: pointer;
        vertical-align: middle;
    }

    &__setting--selected, &__setting--selected &__setting-icon {
        color: @color-enabled;
    }

    &__setting-icon {
        display: inline-block;
        width: 25px;
        padding-left: 10px;
        text-align: left;
        font-size: 25px;
        cursor: pointer;
        vertical-align: middle;
    }

    &__setting:nth-child(3) &__setting-icon i {
        color: #FFB502;
    }
}
div.vertical-line{
  border-left: 1px solid #D9D9D9;
  height: 23px; 
  display: inline-block;
  margin-left:8px;
  vertical-align: middle;
}

.ie8 .settings {
	&__setting--selected:after {
		left: 60px;
	}
}

