.Modal {
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-transition: 0.5s;
    overflow: auto;
    transition: all 0.3s linear;
}

.Modal-content {
    background-color: #FFFFFF;
    margin: auto;
    border-radius: 12px;
    border: solid;
    border-width: 1px;
    width: 310px;
    height: 175px;
    color: #F67C01;
}

.is-hidden { 
    display: none;
}

.is-visuallyHidden {
    opacity: 0;
}

.regionsLock {
    font-family: @kievitregular;
    font-weight: 800;
    height: 100%;

    &__header {
        height: 5%;
        color: #000000;
        font-size: 16px;
        float: right;
        padding: 10px;

        &__button {
            font-size: 12px;
        }
    }

    &__container {
        color: #6F8299;
        text-align: left;
        font-size: 16px;
        padding: 10px;
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 20px;
        height: 83%;
    }

    &__message {
        height: 38px;
        display: inline-block;
        margin-top: 12px;
    }

    &__footer {
        background: #F67C01;
        color: #F67C01;
        height: 17%;
        display: inline-block;
        width: 100%;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        border: solid;
        border-width: 1px;
        margin-top: 1px;

        &__topfill {
            color: #FF920F;
            position:relative;
            height: 20px;
        }

        &__button {
            cursor: pointer;
            color: #FFFFFF;
            font-size: 16px;
            text-align: center;
            height: 100%;
            width: 100%;
            padding-top: 7px;
            font-weight: bold;
        }
    }
}