.header {  
    &__showLogo {
        display: none;
    }
    &__logo-mark {
        color: #1D1D1B;
        .fs-34();
    }
    &__sublogo {
        color: #595C68;

        &__Pro {
            .fs-27();
            color: #595C68;
        }
    }
    &__buttonEnabled {
        &__upgradeEnabled {
            background: #FFA400;
            &:hover {
                background: #FFCA28;
            }    
        }
    }
}

.settings-view {	   
    background-color: #F3F5F6; 
}

.round-button a {   
    font-size: 24px;   
}

.round-button-disabled a .ng-scope{   
    font-size: 24px;   
}

.proButton {   
    width: 30px;
    vertical-align: middle;       
}

.main-view {
    &__pro {
        background-color: #2A3239;
    }
}


.theme-light {    
    .round-button-circle {
        background: #23be04;
    }
    .quit-linktext a > {
        color: blue;
    }
}

.theme-light.vpn-connected {
    .traffic__used {
        color: #49BC13;
    }
    .location__privacy_message {
        color: @color-ok;
    }
}