.pulsar {
    overflow: visible;
    height: 30%;
    width: 100%;
    text-align: center;
    position: relative;
    transform: translateZ(0);
    display: block;   
    
    &__icon {
        position: absolute;
        margin-top: 1px;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        width: 170px;
        height: 170px;

        &_img {
            background: url("../images/png/pulsar_watermark.png") no-repeat center;
            position:relative;
            top:0px;
            width: 170px;
            height: 170px;
            margin-top: 42px;
        }
    }

    &__icon__big {
        position: absolute;
        margin-top: 1px;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        width: 190px;
        height: 190px;
    }
}

@keyframes wave {

    0% {
        border-width: 0px;
        opacity: 0;
    }

    2% {
        border-width: 0px;
        opacity: 0.6;
    }

    98% {
        border-width: 60px;
        opacity: 0;
    }

    100% {
        border-width: 0px;
        opacity: 0;
    }
}

@keyframes pulse-border {

    0% {
        border-width: 2px;
        opacity: 0.6;
    }

    100% {
        border-width: 2px;
        opacity: 0.3;
    }
}

.pulse_border {
    box-sizing: content-box;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    width: 188px;
    height: 188px;
    background-color: transparent;

    .vpn-connecting & {
        animation: pulse-border 1s 0s linear infinite;
    }

    .vpn-diconnecting & {
        animation: pulse-border 1s 0s linear infinite;
    }

    .vpn-connected & {
        display: none;
    }

    .vpn-disconnected & {
        display: none;
    }
}

.pulsing {

    &__wave {
        box-sizing: content-box;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        width: 190px;
        height: 190px;
        background-color: transparent;
        opacity: 0;
        border-width: 0px;
    }

    .vpn-connecting &__wave {
        &:nth-child(1) {
            animation: wave 3s 0s ease-out infinite;
        }

        &:nth-child(2) {
            animation: wave 3s 1s ease-out infinite;
        }

        &:nth-child(3) {
            animation: wave 3s 2s ease-out infinite;
        }
    }

    .vpn-disconnecting &__wave {
        &:nth-child(1) {
            animation: wave 3s 0s ease-out infinite;
        }

        &:nth-child(2) {
            animation: wave 3s 1s ease-out infinite;
        }

        &:nth-child(3) {
            animation: wave 3s 2s ease-out infinite;
        }    
    }

    .vpn-connected &__wave {
        display: none;
    }

    .vpn-disconnected &__wave {
        display: none;
    }
}

.info {

    &__control {
        width: 100%;
        font-size: 13px;
        z-index: 10;
        background: #3B3F3A;
        color: white;
        line-height: 15px;
        position: absolute;
    }

    &__text {
        font-size: 13px;
        padding: 10px;
    }
}

.trial__control {
    background: #FF920F;
    cursor:pointer;
}

.trial__close {
    font-size: 12px;
    color: white;
}

.buttonclose {
    color: grey;
    display: inline-block;
    width: 15px;
    font-size: 15px;
    z-index: 12;
    position: relative;
    float: right;
    margin: 9px 3px;
    cursor: pointer;
}

.trial_slider {
    margin-top: 104px;
    float: right;

    &__shape {
        position: relative;
        height: 0px; 
        border-top: 16px solid #FFB400;
        border-bottom: 16px solid #FFB400;
        border-left: 10px solid transparent;
        transition: max-width 800ms;
        text-align:left;
        white-space:nowrap;
    }

    &__shape:hover {
        cursor: pointer;
    }
    
    &__text{
        color: white;
        font-size: 16px;
        text-align: left;
        margin-top: -7px;
        margin-left: 10px;
        margin-right: 10px;
        display: inline-block;
    }
}
