.Modal {
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-transition: 0.5s;
    overflow: auto;
    transition: all 0.3s linear;
}

.Modal-content {
    background-color: #FFFFFF;
    margin: auto;
    border-style: hidden;
    border-radius: 4px;
    width: 310px;
}

.is-hidden { display: none; }

.is-visuallyHidden {
    opacity: 0;
}

.is-blurred {
    filter: blur(2px);
    -webkit-filter: blur(2px);
}

.rate {
    text-align: center;
    font-size: 16px;
    color: #6F8299;
    padding: 20px;
    height: 100%;
    position: relative;
    &__title {
        font-size: 24px;
        color: #3A4144;
        line-height: 30px;
        display: inline-block;
        margin-top: 28px;
    }
    &__description {
        font-size: 16px;
        color: #6F8299;
        display: inline-block;
        margin-top: 16px;
        margin-bottom: 32px;
        padding-left: 10px;
        padding-right: 10px;
        line-height: 22px;
    }
    &__starsText{
        font-size: 16px;
        font-weight: bold;
        color:  #3A4144;
        height: 20px;
        margin-bottom: 12px;
    }
    &__starsContainer {
        display: inline-block;
        padding-left: 33px;
        padding-right: 33px;
    }
    &__footer {
        position: absolute;
        bottom: 0;
        width: 270px;
    }

    &__buttons{
        height: 40px;

        &__vertical {
            display: table;
            width: 75%;
            padding-left: 10px;
            padding-right: 10px;
            margin: 0 auto;
            margin-bottom: 24px;
        }

        &__horizontal {
            margin-bottom: 24px;
        }
    }

    &__buttonContainer {
       
        &__vertical{
            display: table-row;
            width: 60%;
        }

        &__horizontal {
            display: inline-block;
            width: 45%;
        }
    }
    &__notNow {
      width: 100%;
      height: 27px;
      font-size: 20px;
      color: #6F8299;
      cursor: pointer;
      display: inline-block;
    }
}

.rateButton {
    color: white;
    cursor: pointer;
    border: 1px solid;
    background-color: #23BE05;
    border-radius: 3px;
    font-size: 20px;
    padding-top: 9px;
    padding-bottom: 4px;
    width: 100%;
    height: 40px;
}

.rateButton:hover {
    background-color: #25c804;
}

#dontShowAgainCheckBox {
    position: absolute;
    top: -9999px;
    left: -9999px;
}

#dontShowAgainLabel {
    box-sizing: border-box;
    background: #FFFFFF;
    border: 1px solid #CFD6DC;
    border-radius: 2px;
    display: inline-block;
    width: 18px;
    height: 18px;
    text-align: center;
    margin-right: 8px;
}

#dontShowAgainCheckBox:checked + label:after {
    position: relative;
    top: -1px;
    content: '\2714';
    font-size: 12px;
    color: #99a1a7;
}

#dontShowAgainCheckBox + label:after {
    content: '\2714';
    font-size: 12px;
    color: #FFFFFF;
}

.dontShowAgain {
    margin-bottom: 24px;
}

@supports (-ms-ime-align:auto) {
    input[type=checkbox]+label:after {
        content: '\00a0';
    }
}