.emailConfirmation {
    display: table;
    text-align: center;
    width:100%;
    height: 100%;
    line-height: 25px;
    font-size: 18px;

    &__middle {
        text-align: center;
        width: 310px;
        height: 100%;
        display: inline-block;
    }

    &__emailInfo {
        margin-top: 30px;

        &__textBox {
            background: #FFFFFF;
            border: 1px solid #CFD6DC;
            border-radius: 2px;
            height: 50px;
        }

        &__textField {
            width: 310px;
            text-align: left;
            padding: 15px;
            color: #3A4144;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    &__emailButtons {
        margin-top: 12px;
        font-size: 14px;
        display: table;
        width: 100%;
        
        &__emailSent {
            color: #23BE04;
            display: table-cell;
            text-align: left;
            float: left;
        }

        &__resendEmail {
            color: #2B88F6;
            display: table-cell;
            text-align: right;
            cursor: pointer;
            float: right;
        }
    }

    &__description {
        display: table-cell;
        vertical-align: middle;

        &__container {
            height: 25%;
            display: table;
        }

        span {
            margin-top: 30px;
            margin-bottom: 30px;
            display: inline-block;
        }
    }

    &__footer {
        display: table-row;
        vertical-align: bottom;
        width: 100%;
        height: 65px;
        color: #2B88F6;

        span {
            display: inline-block;
            cursor: pointer;
        }
    }
}