.privacyDescription {      
    display: table;  
    height: 63%;
    min-height: 286px;
    width: 100%;

    &__container {
        &__wrapper {
            display: table; 
            height: 100%; 
            margin-top: 20px;
            margin-bottom: 20px;
            width: 100%;
        }

        display: table-cell;
        vertical-align: middle;
        font-size: 18px;
        text-align: center;
    }

    &__text {
        display: inline-block;
        text-align: left;
        width:100%;
        line-height: 19px; 

        &__container {
            display: inline-block;
            text-align: left;
            width:80%;
            max-width: 535px;    
        }

        &__bulletPoint {
            span {
                display: list-item;
                list-style-type: disc;
                list-style-position: inside;
                width: 100%;
            }
        }

        &__bold {
            font-family: @kievitmedium;
        }

        span {
            width: 100%;
        }
    }
}

.privacyFooter {
    display: table-row;
    vertical-align: bottom;
    width: 100%;
    height: 110px;
    background-color: white;
  
    &__container {
        border-top: 1px solid @border-color;
        display: table-cell;
        vertical-align: middle;
        text-align: center;
    }

    &__button {
        display: inline-block;
        color: white;
        cursor: pointer;
        border: 1px solid;
        background-color: #23BE04;
        border-radius: 2px;
        font-size: 22px;
        padding: 14px;
        width: 310px;
    }   
}
