.theme-dark {
    @secondary-text-color: #FFFFFF;
    @font-color-logo: #FFFFFF;
    @color-pulsar-disconnected: #3B4C54;
    @color-pulsar-border-disconnected: #6B6D6F;
    @color-pulsar-shadow-disconnected: #232a2f;
    @header-background: #161F25;
    @subheader-background: #2A3239;
    @generic-border-color: #2A3239;
    @status-background: #2A3239;
    @content-background: #161F25;
    color: @secondary-text-color;
    
    .unselectable {
        color: @secondary-text-color;
    }
    
    .emphasized {
        color: #FFFFFF;
    }

    .diagnostic_data {
        &__referenceId {
            background-color: #3d4446;
        }
    }

    a {
        color: @secondary-text-color;
    }

    .guiFrame {
        border-color:#2A3239;
    }

    .header {
        background-color: @header-background;
        border-bottom: 1px solid @generic-border-color;
        border-top: 1px solid @generic-border-color;
        &__logo-mark {
            color: @secondary-text-color;
        }
        &__sublogo {
            color: @secondary-text-color;
        }
        &__sublogo__Pro {
            color: @secondary-text-color;
        }
    }

    .main {
        height: 100%;
        background: url(../images/png/Map_pro.png) no-repeat;
        background-size: 140%;
        background-position: bottom;
    }
    
    .main-uwp {
        height: 100%;
        background: url(../images/png/Map_pro.png) no-repeat center;
    }

    .header__button__account.loggedin {
        background-color: #AAB8C2;
        color: #3b4446;
    }
    .header__button__account.loggedin:hover {
        background-color: #bac8d1;
        color: #3b4446;
    }
    .location {
        hr {
            visibility: hidden;
            border: none;
            border-top: 1px solid #3d4446;
        }
    }
    .pulsar__icon {
        background-color: @color-pulsar-disconnected;
        &__big {
            background-color: @color-pulsar-border-disconnected;
            box-shadow: 0px 1px 15px @color-pulsar-shadow-disconnected;
        }
    }
    .round-button-circle {
        background: #49BC13;
    }
    .round-button-circle-disabled {
        background: @content-background;
        border: 1px solid #BDBDBD;
    }
    .round-button a {
        color: #FFFFFF;
    }
    .round-button-disabled a {
        color: #bac8d1;
    }
    .about-Avira {
        &__link {
            color: @secondary-text-color;
            text-decoration: underline;
        }
    }
    .theme-selection {
        background-color: @status-background;
    }
    .settings-view {
        background-color: @content-background;
    }
    .settings-switch {
        background-color: @content-background;
        border-bottom: 1px solid @generic-border-color;

        &__text {
            &__description {
                color: rgba(255,255,255,0.5);
            }
        }
    }

    .settings {
        border-bottom: 1px solid #2f3435;
        background-color: @subheader-background;
        &__setting--selected,
        &__setting--selected &__setting-icon {
            color: @color-enabled;
        }
        &__quickSearch {
            &__textBox {
                border: 1px solid #2f3435;
            }
        }
    }
    .regions {
        border-bottom: 1.5px solid @generic-border-color;
    }
    div.vertical-line {
        border-left: 1px solid #6e7b7f;
    }
    .regions-switch {
        &:hover {
            background-color: darken(@content-background, 6%);
        }
    }
    .wifiNano{
        &:hover {
            background-color: darken(@content-background, 6%);
        }
    }
    .selectedItem {
        background-color: darken(@content-background, 6%);
    }
    .switch {
        background: #535d60;
        border: 1px solid #40474a;
    }
    .location__current {
        color: @secondary-text-color;
    }
    .location__caret {
        color: @secondary-text-color;
    }
    .dropdown {
        &__popup {
            background: #535d60;
            border: 1px solid #40474a;
        }
        &__option {
            background: #535d60;
            color: @secondary-text-color;
        }
    }
    .header_menu_content:before {
        border-color: transparent transparent @content-background transparent;
    }
    .header_menu_content:after {
        border-color: transparent transparent #3d4446 transparent;
    }
    .header_menu_content {
        border-color: @generic-border-color;
        background: @content-background;
    }
    .header_menu_content a {
        color: @secondary-text-color;
        background: @content-background;
    }
    .header_menu_content a:hover {
        color: @secondary-text-color;
        background: @status-background;
    }
    .header__button__dropdown {
        background: url(../images/png/angle-down-dark.png) no-repeat center;
    }
    .privacyFooter__container{
        border-top: 1px solid #2f3435;
        background-color: @header-background;
    }
    .privacyFooter__button{
        border: 1px solid #2f3435;
    }
    .wificonnected {
        color: white;
    }
    .wifi-icon-connected {
        background: url(../images/png/wifi-connected-dark.png);
        width: 18px;
        height: 18px;
    }
    .wifi-icon-disconnected {
        background: url(../images/png/wifi-disconnected-dark.png);
        width: 18px;
        height: 18px;
    }
    .radiobutton small {
        background: #535d60;
    }
    .loader:before {
        background: @content-background;
    }
    .loader:after {
        background: @content-background;
    }
    .checkmark {
        background-color: transparent;
        border: 1px solid #6B6D6F;
    }
    .radioContainer input:checked~.checkmark {
        background-color: #057BBC;
        border: 1px solid #3AACFC;
    }
    .radioContainer .checkmark:after {
        background: white;
    }
    .puchasePage {
        background: @content-background;
    }

    .purchase {
        background: @subheader-background;
        
        &__description {

            &__box {
                background: @content-background;
            }
        }
    }

    .subscriptionTerms {
        background: @subheader-background;
    }

    .input-group {
        &__field {
            color: #ffffff;
        }
    }
}

.theme-dark.vpn-connected {
    @color-pulsar-connected: #435055;
    @color-pulsar-shadow-connected: #232a2f;
    @secondary-text-color: #bac8d1;
    @font-color-logo: #bac8d1;

    .traffic__used {
        color: #49BC13;
    }
    .location__privacy_message {
        color: @color-ok;
    }
    .pulsar__icon {
        background-color: @primary-text-color;
    }
    .pulsar__icon__big {
        background-color: @color-pulsar-connected;
        box-shadow: 0px 1px 15px @color-pulsar-shadow-connected;
    }
    .pulse_border {
        border: 2px solid #23BE04;
    }
    .pulsing__wave {
        border: 0px solid #23BE04;
    }
    .connect-button-circle {
        background: #586063;
        border: 0px solid #464c4e;
        .prefix(box-shadow, ~"0px 1px 1px 1px rgba(70, 76, 78, 0.2), 0px -1px 1px 0px rgba(70, 76, 78, 0.2)");
    }
    .connect-button a {
        color: @secondary-text-color;
    }
}

.theme-dark.vpn-disconnected {
    .location__privacy_message {
        color: @color-nok;
    }
}

.theme-dark.vpn-disconnecting,
.theme-dark.vpn-connecting {
    @secondary-text-color: #bac8d1;
    @font-color-logo: #bac8d1;
    @color-pulsar-disconnected: #3B4C54;
    .pulse_border {
        border: 2px solid @color-pulsar-disconnected;
    }
    .pulsing__wave {
        border: 0px solid @color-pulsar-disconnected;
    }
    .connect-button-circle {
        background: #586063;
        border: 0px solid #464c4e;
        .prefix(box-shadow, ~"0px 1px 1px 1px rgba(70, 76, 78, 0.2), 0px -1px 1px 0px rgba(70, 76, 78, 0.2)");
    }
    .connect-button a {
        color: @secondary-text-color;
    }
}