.keychain {
    display: table;
    font-family: @kievitlight;
    font-size: 16px;
    color: #6F8299;
    text-align: center;
    width: 100%;
    height: 100%;

    &__title {
        font-size: 24px;
        height: 67px;
        width: 100%;
        display: table;
        vertical-align: middle;
        background: white;
        border-bottom: 1px solid #C1CCD2;

        span {
            display: table-cell;
            vertical-align: middle;
        }
    }

    &__body {
        height: 100%;
        width: 100%;
        background: #F3F5F6;
        display: table-row;

        &__imgDialog {
            display: inline-block;
        }

        &__imgMagnifier {
            position: fixed;
            left: 46px;
            top: 68px;
            z-index: 8888;
        }

        &__alwaysAllow {
            font-family: Helvetica;
            color: black;
            position: fixed;
            top: 180px;
            z-index: 8888;
            padding: 3px;
            padding-left: 5px;
            padding-right: 5px;
            border-radius: 4px;
            box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.39);
            background: white;
        }

        &__description {
            width: 310px;
            display: inline-block;
            line-height: 25px;
        }
    }

    &__footer {
        display: table-row;
        vertical-align: bottom;
        width: 100%;
        height: 100px;
        background: #F3F5F6;

        &__button {
            display: inline-block;
            color: white;
            cursor: pointer;
            border: 1px solid;
            background-color: #2B88F6;
            border-radius: 2px;
            font-size: 22px;
            padding: 14px;
            width: 310px;
        }
        
        &__checkbox {
            display: inline-block;
            width: 18px;
            height: 14px;
        }
    }
}

.checkBoxContainer {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Hide the browser's default checkbox */
.checkBoxContainer input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

/* Create a custom checkbox */
.checkBoxCheckmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 18px;
    width: 18px;
    background-color: white;
    border: 1px solid #C1CCD2;
    border-radius: 2px;
}

/* When the checkbox is checked, add a blue background */
.checkBoxContainer input:checked ~ .checkBoxCheckmark {
    background-color: #2B88F6;
    border: 1px solid #2B88F6;
    border-radius: 2px;
}

/* Create the checkBoxCheckmark/indicator (hidden when not checked) */
.checkBoxCheckmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkBoxCheckmark when checked */
.checkBoxContainer input:checked ~ .checkBoxCheckmark:after {
    display: block;
}

/* Style the checkBoxCheckmark/indicator */
.checkBoxContainer .checkBoxCheckmark:after {
    left: 5px;
    top: 2px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}