.forced-login {       
    text-align: center;

    &__title {   
        font-size: 32px;       
        line-height: 43px;
    }

    &__subtitle {        
        font-size: 18px;   
        line-height: 25px;     
    }

    &__input-box {                
        height: 50px;
        width: 310px;        
        border: 1px solid #6B6D6F;    
        border-radius: 2px;                
        display: inline-block;
        margin-bottom: 3px;          
    }
    
    &__input-field {
        font-size: 18px;   
        color: #ffffff;
        height: inherit;
        width: inherit;
        background-color: transparent;
        border: none;
        padding-left: 15px;
        font-family: @kievitlight;

        &::-webkit-input-placeholder { /* Edge */
            color: #ffffff;            
          }

        &:-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: #ffffff;            
          }
          
        &::placeholder {
            color: #ffffff;            
          }
    }

    &__text-box {                
        width: 310px;        
        display: inline-block;            
    }

    &__forgot-password-text {
        font-size: 14px;      
        color: #FFFFFF;        
        float: right;        
        font-style: italic; 
    }

    &__button {        
        height: 50px;
        width: 310px;        
        background-color: #23BE04;        
        border-radius: 2px;                
        display: table;
        margin-bottom: 25px;  
        cursor: pointer;             
    }

    &__button-text {
        font-size: 22px;                      
        height: inherit; 
        display: table-cell;
        vertical-align: middle;       
    }

    &__error-text {
        font-size: 14px;
        color: #F20202;
        margin-bottom: 6px;  
        float: left;          
    }
}

.overlay-text {
    font-size: 18px;
}

.forced-login-background {
    display: none;        
}

.fl-loader,
.fl-loader:after {
  border-radius: 50%;
  width: 4em;
  height: 4em;
}

.fl-loader-overlay {
    z-index:10;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: auto;
    -ms-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);   
}

.fl-loader-overlay-text {
    z-index: 10;
    position: absolute;
    top: 56%;
    left: 50%;
    margin: auto;
    -ms-transform: translateX(-50%) translateY(-50%);
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);   
 }

.fl-loader {
  font-size: 9px;
  text-indent: -9999em;
  border-top: 0.4em solid rgba(255, 255, 255, 0.2);
  border-right: 0.4em solid rgba(255, 255, 255, 0.2);
  border-bottom: 0.4em solid rgba(255, 255, 255, 0.2);
  border-left: 0.4em solid #ffffff;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}