.waitingWindow {
    display: table;
    text-align: center;
    width:100%;
    color: #6F8299;	
    font-size: 18px;	
    background-color: white;
    height: 100%;

    &__pulsarContainer {
        display:table-raw; 
        height: 110px; 
        width: 100%;
    }

    &__connecting {
        display: inline-block;
        width: 100%;
        font-size: 24px;  
        color: #3A4144;
        margin-top: 43px;
        
        &__virtualLocation {
            font-size: 16px;	
            line-height: 22px;	
            margin-top: 4px;
        }        
    }

    &__waitingTime {
        display: inline-block;
        width: 100%;
        margin-top: 36px;
        line-height: 25px;

        a {
            font-family: @kievitmedium;
        }
    }

    &__question {
        display: inline-block;
        width: 100%;
        margin-top: 32px;
        font-size: 16px;	
        line-height: 22px;
    }

    &__getPro {	
        margin-top: 16px;
        display: table;
        height: 48px;	
        width: 240px;	
        border-radius: 25px;	
        background-color: #FFA400;

        span {
            font-size: 24px;	
            line-height: 33px;
            color: #FFFFFF;	
            font-size: 24px;	
            line-height: 33px;	
            text-align: center;
            display: table-cell;
            vertical-align: middle;
            cursor: pointer;
        }
    }

    &__cancel {
        display: inline-block;
        margin-top: 24px;
        color: #6F8299;
        text-decoration: underline;
        font-size: 16px;
        line-height: 12px;	
        width: 100%;

        span {
            cursor: pointer;
        }
    }
}
