@import "flex";

body.notembedded {
	margin: 0px;
}

.inapp-debug {
	position: absolute;
	display: inline-block;
	right: 0;
	bottom: 0;
	width: 500px;
	height: 400px;
	border: 1px solid @border-color;
	border-width: 1px 0 0 1px;
	border-radius: 2px 0 0 0;
	background: white;

	.transition(height 0.1s, width 0.1s;);

	input {
		outline: none;
	}

	& > div {
		position: relative;
		width: 100%;
		height: 100%;
	}

	&__header {
		height: 30px;
		line-height: 30px;
		font-size: 12px;
		font-weight: bold;
		padding: 0 10px;
		border-bottom: 1px solid @border-color;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		background: #ffffff; /* Old browsers */
		.prefix(background, linear-gradient(to bottom,  #ffffff 0%,#e5e5e5 100%));
		.prefix(user-select, none);
	}

	&__reload {
		float: right;
	}

	&__body {
		float: left;
		position: absolute;
		top: 30px;
		right: 0;
		bottom: 0;
		left: 0;
		padding: 5px;

		.flex-display();
		.flex-direction(column);

		.pathahead {
			.flex-grow(0);

			input {
				width: 100%;
				outline: none;
				height: 32px;
				line-height: 30px;
				padding: 0 10px;
				margin-bottom: 5px;
				.border();
			}
		}	

		.twitter-typeahead {
			width: 100%;
		}

		.tt-menu {
			width: 100%;
			.border();

			.tt-suggestion {
				line-height: 32px;
				font-size: 12px;
				padding: 0 10px;
				background: white;
			}
			.tt-suggestion.tt-cursor {
				background: @border-color;
			}
		}

		.CodeMirror {
			.flex-grow(1);
			width: 100%;
			border: 1px solid @border-color;
			color: #333;
			font-size: 14px;
			font-family: Courier;
			outline: none;
		}
	}

	&--minimized &__body {
		display: none;
	}

	&__actions {
		padding: 4px 0px;
		min-height: 40px;
		input {
			float: right;
			height: 32px;
			background: white;
			.border();
			border-radius: 2px;
			cursor: pointer;
			color: @color-enabled;
			padding: 0 20px;
			.transition(background-color 0.35s);
			&:hover {
				background-color: @border-color;
			}
		}
	}

	&__resize-left {
		position: absolute;
		background: transparent;
		cursor: ew-resize;
		width: 3px;
		left: -1px;
		top: 0;
		bottom: 0;
	}

	&__resize-up {
		position: absolute;
		background: transparent;
		cursor: ns-resize;
		height: 3px;
		left: 0;
		top: -1px;
		right: 0;	
	}
}

.CodeMirror pre {
	font-size: 11px;
}