.diagnostic_data {
    display: table;
    text-align: center;
    width:100%;
    line-height: 25px; 
    font-size: 18px;

    &__start{

        &__description .ng-scope {
            font-family: KievitCompPro;            
            font-size: 20px;
            line-height: 28px;
        }

        &__lastReportTitle .ng-scope {
            font-family: KievitCompPro;
            font-size: 12px;
            letter-spacing: 0.5px;
            line-height: 16px;
        }

        &__lastReportDate .ng-scope{
            font-family: KievitCompPro;
            font-size: 16px;
            line-height: 24px;
        }

        &__sendButtonContainer {
            bottom: 0px;
        }
    }

    &__collect {
        &__question {
            font-family: KievitCompPro;
            font-size: 16px;
            line-height: 24px;
            font-weight: 600;
        }

        &__data {
            margin-left: 25px;
            min-width:300px;
            text-align: left;

            &__text .ng-scope{
                font-family: KievitCompPro;
                font-size: 16px;
                line-height: 24px;
            }
        }
    }

    &__send {
    
        &__description .ng-scope {
            font-family: KievitCompPro;
            font-size: 16px;
            line-height: 24px;
        }

        &__data .ng-scope{
            font-family: KievitCompPro;
            font-size: 16px;
            line-height: 24px;
            font-weight: 700;
        }

        &__details .ng-scope{
            font-family: KievitCompPro;
            font-size: 14px;
            letter-spacing: 0.5px;
            line-height: 20px;
        }
    }

    &__progress {
        &__img_stars {
            position: relative;
            top: 80px;
            left: -5px;
        }

        &__status {
            font-family: KievitCompPro;
            font-size: 24px;
            line-height: 33px;
        }
    }

    &__confirm {
        &__title .ng-scope{
            font-family: KievitCompPro;
            font-size: 20px;
            line-height: 28px;
        }

        &__description .ng-scope{
            font-family: KievitCompPro;
            font-size: 16px;
            line-height: 24px;
        }

        &__reference .ng-scope{
          font-family: KievitCompPro;
          font-size: 14px;
          line-height: 20px;
          letter-spacing: 1px;
        }
    }

    &__referenceId{
      font-family: KievitCompPro;  
      font-size: 13px;  
      line-height: 20px;
      letter-spacing: 0.86px;
      border: 1px solid #BDBDBD;
      border-radius: 3px;
      padding: 3px;
  }

  &__referenceIdMarked{
    font-family: KievitCompPro;
    font-size: 13px;  
    line-height: 20px;
    letter-spacing: 0.86px;
    border: 1px dashed #BDBDBD;
    border-radius: 3px;
    padding: 3px;
    background-color: none;
}

  &__referenceIdCopy{
    font-size: 16px;  
    line-height: 24px;
    color: #3AACFC;
    border: 1px solid #3AACFC;
    border-radius: 3px;
    padding: 1px;
  }

  &__referenceIdCopied{
    font-family: KievitCompPro;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
    color: #22BD03;
  }
}

textarea {
    resize: none;
    height:100px;
    width:278px;
    border-radius: 3px;
    border: 1px solid #CFD6DC;
    font-family: KievitCompPro;
    font-size: 14px;
    line-height: 18px;
}

.unstyled {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

.styled-checkbox {
    position: absolute;
    opacity: 0;
  
    & + label {
      position: relative;
      cursor: pointer;
      padding: 0;
    }
  
    & + label:before {
      content: '';
      margin-right: 3px;
      display: inline-block;
      vertical-align: text-top;
      width: 16px;
      height: 16px;
      background: white;
      box-sizing: border-box;
      border-radius: 2px;
      border: 1px solid #CFD6DC;
    }
  
    &:checked + label:after {
        content: '\2714';
        position: absolute;
        left: 3px;
        top: -5px;
        font-size: 12px;
        color: #99a1a7;
    }
  }

.all-copy {
    -webkit-user-select: text;  /* Chrome all / Safari all */
    -moz-user-select: text;     /* Firefox all */
    -ms-user-select: text;      /* IE 10+ */
    user-select: text;          /* Likely future */   
}


.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
}
.loader {
  color: #5AA8FC;
  font-size: 10px;
  text-indent: -99999em;
  margin: 10px auto;
  position: relative;
  width: 10em;
  height: 10em;
  box-shadow: inset 0 0 0 1em;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before,
.loader:after {
  position: absolute;
  content: '';
}
.loader:before {
  width: 5.4em;
  height: 10.4em;
  border-radius: 10.4em 0 0 10.4em;
  top: -0.2em;
  left: -0.2em;
  -webkit-transform-origin: 5.2em 5.1em;
  transform-origin: 5.2em 5.1em;
  -webkit-animation: load2 2s infinite ease 1.5s;
  animation: load2 2s infinite ease 1.5s;
}
.loader:after {
  width: 5.4em;
  height: 10.4em;
  border-radius: 0 10.4em 10.4em 0;
  top: -0.1em;
  left: 5.1em;
  -webkit-transform-origin: 0px 5.1em;
  transform-origin: 0px 5.1em;
  -webkit-animation: load2 2s infinite ease;
  animation: load2 2s infinite ease;
}
@-webkit-keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load2 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}