.trial {
    text-align: center;
    margin-top: 100px;

    &__title {
        margin-bottom: 5px;
        color: @color-enabled;
        font-size: 24px;
        width: 100%;
        margin-bottom: 25px;
        line-height: 28px;
    }

    &__description {
        margin: auto;
        color: @color-disabled;
        font-size: 18px;
        width: 80%;
        margin-bottom: 80px;
        line-height: 25px;
    }

    &__registerButton {
        margin: auto;
        width: 150px;
        height: 37px;
        background: #23be04;
        text-align: center;
        color: white;
        font-size: 20px;
        vertical-align: middle;
        padding-top: 8.5px;
        border-radius: 2px;
        cursor: pointer;
        margin-bottom: 25px;
    }

    &__notNowButton {
        cursor: pointer;
        font-size: 14px;
        color: @color-disabled;
        display: inline-block;
    }
}
