@font-face {
	font-family: 'Kievit Regular';
	src: url("../fonts/KievitWebPro-Book.eot");
	src: local('Kievit Regular'), url("../fonts/KievitWebPro-Book.woff") format('woff');
}


@font-face {
	font-family: 'Kievit Medium';
	src: url("../fonts/KievitWebPro-Medi.eot");
	src: local('Kievit Medium'), url("../fonts/KievitWebPro-Medi.woff") format('woff');
}


@font-face {
    font-family: 'KievitCompPro';
	src: url("../fonts/KievitCompPro.woff") format('woff');
	src: local('KievitCompPro'), url("../fonts/KievitCompPro.woff") format('woff');
}


// Kievit Light
@font-face {
	font-family: 'Kievit Light';
	src: url("../fonts/KievitWeb-Light.eot");
	src: local('Kievit Light'), url("../fonts/KievitWeb-Light.woff") format('woff');
}


// Kievit variables
@kievitmedium: "Kievit Medium", Helvetica, Arial;
@kievitmedium-italic: "Kievit Medium Italic", Arial, Verdana, sans-serif;
@kievitregular: "Kievit Regular", Helvetica, Arial;
@kievitregular-italic: "Kievit Regular Italic", Arial, Verdana, sans-serif;
@kievitlight: "Kievit Light", Helvetica, Arial, Verdana, sans-serif;
@kievitlight-italic: "Kievit Light Italic", Helvetica, Arial, Verdana, sans-serif;

