.theme-light {
    @color-pulsar-shadow-disconnected: #E2E2E2;
    color: @secondary-text-color;

    .unselectable {
        color: @secondary-text-color;
    }

    .emphasized {
        color: #3D3C40;
    }

    a {
        color: @secondary-text-color;
    }

    .main-view {
        background-color: white;
    }

    .guiFrame {
        border-color: #9a9b9b;
    }
    
    .header {
        background-color: white;
        border-bottom: 1px solid #D9D9D9;

        &__logo-mark {
            color: #1D1D1B;
        }

        &__sublogo {
            color: #595c68
        }

        &__sublogo__Pro {
            color: #595c68;
        }

        &__account a {
            color: white;
        }
    }
    
    .main {
        height: 100%;
        background: url(../images/png/map.png) no-repeat;
        background-size: 140%;
        background-position: bottom;
    }
    
    .main-uwp {
        height: 100%;    
        background: url(../images/png/map.png) no-repeat center;
    }

    .location {
        hr {
            visibility: hidden;
            border: none;
            border-top: 1px solid #C1CCD2;
        }
    }
   
    .round-button-circle-disabled {
        background: #F3F5F6;
        border: 1px solid #BDBDBD;
    }
    .round-button a {
        color: white;
    }
    .round-button-disabled a {
        color: #3D3C40;
    }
    .pulsar__icon {
        background-color: @color-pulsar-disconnected;

        &__big {
            background-color: white;
            box-shadow: 0px 1px 15px @color-pulsar-shadow-disconnected;
        }
    }
    .header_menu_content:before {
        border-color: transparent transparent #cfd6dc transparent;
    }

    .header_menu_content {
        border-color: #cfd6dc;
    }

    .header_menu_content a {
        color: #7e93a7;
    }

    .header_menu_content a:hover {
        color: #595c68;
    }

    .header__button__dropdown {
        background: url(../images/png/angle-down-light.png) no-repeat center;
    }

    .wificonnected {
        color: black;
    }

    .wifi-icon-connected {
        background: url(../images/png/wifi-connected-light.png);
        width: 18px;
        height: 18px;
    }

    .wifi-icon-disconnected {
        background: url(../images/png/wifi-disconnected-light.png);
        width: 18px;
        height: 18px;
    }


    .regions-switch {
        &:hover {
            background-color: darken(#F3F5F6, 6%);
        }
    }

    .wifiNano {
        &:hover {
            background-color: darken(#F3F5F6, 6%);
        }
    }

    .selectedItem {
        background-color: darken(#F3F5F6, 6%);
    }

    .loader:before {
        background: #F3F5F6;
    }
      .loader:after {
        background: #F3F5F6;
    }

    .diagnostic_data {
        &__referenceId {
            color:#6F7073;
            background-color: #FFFFFF;
        }

        &__referenceIdMarked {
            color:#6F7073;
        }
    }

    .theme-selection {    
        &__title {
            color: #3D3C40;
        }
    
        &__options {
            &__radio {
                &__text {
                    color: #3D3C40;
                }
    
                &__subtext {
                    color : #3D3C40;
                }
            }
        }
    
        &__cta2 {
            color: #6F8299;
        }
    }

    .purchase {
        &__title {
            span {
                color: #1D1D1B;
            }
        }

        &__description {
            &__box {
                background: white;
                color: #595C68;
            }
        }

        &__option {
            &__box {
                &__text {
                    color: #7E93A7;
                    &__selected {
                        color: #595C68;
                    }
                }
            }
        }
    }

    .purchaseButtons {
        background: white;
    }
}

.theme-light.vpn-connected {

    @color-pulsar-shadow-connected: #E2E2E2;

    .pulse_border {
        border: 2px solid @color-pulsar-connected;
    }

    .pulsing__wave {
        border: 0px solid @color-pulsar-connected;
    }

    .pulsar__icon {
        background-color: @color-pulsar-connected;

        &__big {
            background-color: white;
            box-shadow: 0px 1px 15px @color-pulsar-shadow-connected;
        }
    }

    .connect-button-circle {
        border: 0px solid @secondary-text-color;
        .prefix(box-shadow, ~"0px 1px 1px 1px rgba(129, 147, 166, 0.2), 0px -1px 1px 0px rgba(129, 147, 166, 0.2)");
        background: white;
    }
    .connect-button a {
        color: @secondary-text-color;
    }
}


.theme-light.vpn-disconnected {
    .location__privacy_message {
        color: @color-nok;
    }
}

.theme-light.vpn-disconnecting, .theme-light.vpn-connecting {
    .pulse_border {
        border: 2px solid @color-pulsar-disconnected;
    }

    .pulsing__wave {
        border: 0px solid @color-pulsar-disconnected;
    }

    .connect-button-circle {
        border: 0px solid @secondary-text-color;
        .prefix(box-shadow, ~"0px 1px 1px 1px rgba(129, 147, 166, 0.2), 0px -1px 1px 0px rgba(129, 147, 166, 0.2)");
        background: white;
    }

    .connect-button a {
        color: @secondary-text-color;
    }
}
