@color-settings-unchecked:  #BAC4C9;

.switch {
  background: #fff;
  border: 1px solid  #A7B3B6;
  position: relative;
  display: inline-block;
  box-sizing: content-box;
  overflow: visible;
  width: 40px;
  height: 20px;
  padding: 0px;
  margin: 0px;
  border-radius: 20px;
  cursor: pointer;
  box-shadow: rgb(223, 223, 223) 0px 0px 0px 0px inset;
  transition: 0.3s ease-out all;
  -webkit-transition: 0.3s ease-out all;
  top: -1px;
  font-size:8px;
}
/*adding a wide width for larger switch text*/
.switch.wide {
  width:80px;
}

.switch.checked small:before {
   font-family: ionicons; 
   content: "\f383";//ion-android-done
} 

.switch small:before {
   font-family: ionicons; 
   content: "\f129";//ion-close-round
} 

.switch small {
  background: #fff;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  position: absolute;
  text-align: center;
  top: 2px;
  left: 2px;
  transition: 0.3s ease-out all;
  -webkit-transition: 0.3s ease-out all;
  background:@color-settings-unchecked;
  padding: 3px 0px;
  font-size:10px;
  color: #889398;
}

.switch.checked small {
  left: 21px;
  background: @primary-text-color;
  padding: 4px 5px;
  color: white;
}

/*wider switch text moves small further to the right*/
.switch.wide.checked small {
  left:40px;
}
/*styles for switch-text*/
.switch .switch-text {
 // font-family:Arial, Helvetica, sans-serif;
  //font-size:23px;
}

.switch .off {
  display:block;
  position: absolute;
  right: 10%;
  top: 25%;
  z-index: 0;
  color:#A9A9A9;
}

.switch .on {
  display:none;
   z-index: 0;
  color:#fff;
  position: absolute;
  top: 25%;
  left: 9%;
}

.switch.checked .off {
  display:none;
}

.switch.checked .on {
  display:block;
}

.switch.disabled {
  opacity: .50;
  cursor: not-allowed;
}
.showDiv
{
  visibility:visible !important;
}
