@font-face {
    font-family: 'font-icons';
    src: url('../fonts/avira-icons-regular-webfont.eot');
    src: url('../fonts/avira-icons-regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/avira-icons-regular-webfont.woff') format('woff'),
         url('../fonts/avira-icons-regular-webfont.ttf') format('truetype'),
         url('../fonts/avira-icons-regular-webfont.svg#font-iconsregular') format('svg');
    font-weight: normal;
    font-style: normal;
}

.icon {
    font-family: "font-icons";
    font-size: inherit;
    display: block;
    position: relative;
    margin: 0 auto;
    text-align: center;
    font-style: inherit;
    line-height: inherit;
    text-rendering: auto;
    text-decoration:none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    :after {
        display: inline-block;
    }
    :before {
        display: inline-block;
    }
}

