.vpn-status {
    height: 19%;
    display: table;
    width: 100%;
    
    &__box {
        display: table-cell;
        vertical-align: middle;
    }

    .license-is-expiring & {
        height: @status-height-license-renew;
        padding-top: 4px;
    }

    .vpn-showRegisterOption & {
        margin-top: -5px;
        padding-top: 0;
        height: 89px;
    }

    .round-button-circle {
        .vpn-disconnecting & {
            visibility: hidden;
        }
    }

    &__ip {
        font-size: 16px;	
        text-align: center;
        margin-bottom: 19px;
        margin-top: 15px;

        &__color {
            font-family: @kievitmedium;
            .vpn-connected & {
                color: @primary-text-color;
            }
        }
    }
}

.register_message {
    color: @secondary-text-color;
    .fs-18();
    text-align: center;
    padding-top: 10px;
    visibility: hidden;

    .vpn-showRegisterOption & {
        visibility: visible;
    }

    &__link {
        color: @link-text-color;
        .fs-18();
    }
}
