.traffic {
	text-align: center;
	height:20%;
    display: block;

   .vpn-showRegisterOption &
    {
        margin-top: -10px;
    }

	.vpn-connected &__used {
		padding-top: 45px;
	}

	.vpn-connected &__limit {
		padding-top: 25px;	
	}

	.vpn-disconnected &__used {
		padding-top: 45px;
	}

	.vpn-disconnected &__limit {
		padding-top: 25px;	
	}

	.vpn-connecting  & {
		visibility: hidden;
	}

	.vpn-disconnecting  & {
		visibility: hidden;
	}

	
	&__bold {
		font-weight: bold;
	}

	&__used {
		.fs-34();
		line-height: 0px;
		font-weight: normal;
        padding: 0 5px;
	}

	&__limit {
		.fs-18();
        padding: 0 5px;
	}
}