.has-svg .icon {
    width: 100%;
    height: 100%;
}

.insider {
    display:block;
    font-size:12px;
    margin-top:-3px;
    margin-left:4px;
    visibility: hidden;

    .is-insider & {
        visibility:visible;
    }
}

.account_max_width {
    white-space: nowrap;
    max-width:85px;
    text-overflow:ellipsis;
    overflow: hidden;
}

.header {
    height: @header-height;
    width: 100%;
    padding: 28px;
    padding-bottom: 17px;
    padding-left: 13px;
    position: relative;   
    float: left;
    z-index: 100;
    font-size: 18px;
    display: block;
    margin-top: -1px;
    border-top: 1px;
    border-top-style: solid;

    .uwp-app & {
        height: 50px;
        padding-top:3px;
        border-top: none;
        border-style: none;
    }

    &__logo {
        width: 40px;
        height: 40px;
        float: left;
        margin-top: 2px;
    }

    &__logo_text {
        height: 40px;
        float: left;
        margin-top: 0px;

        .vpn-macapp & {
            margin-top: -8px;
        }
    }

    &__logo-mark {
        float: left;        
        line-height: 0px;
        margin-left: 8px;
        margin-top: 26px;

        .uwp-app & {
            display: none;
        }
    }


    &__sublogo {
        .fs-27();
        line-height: 0px;
        margin-left: -4px;        

        &__Pro {            
            line-height: 0px;
            margin-left: -4px;
            
            visibility: hidden;

            &__show {
                visibility: visible;
            }
        }
    }

    &__controls {
        position: absolute;
        top: 0;
        right: 0;
        padding: 0;
        margin-top: 3px;
        margin-right: 15px;

        &__frame {
            display: table;
        }

        .uwp-app & {
            margin-top: 13px;
        }
    }

    &__control {
        display: table-cell;
        vertical-align: middle;

        &__disabled {
            visibility: hidden;
            cursor: default;
        }

        a {
            opacity: 0.6;
            filter: alpha(opacity=60);
            color: #6A7477;
            display: inline-block;
            line-height: 21px;
            text-align: center;
            vertical-align: central;
            padding-bottom: 0px;
            text-decoration: none;
            font-size: 90%;
            cursor: default;
        }
    }

    &__controlHover {
        a {
            cursor: pointer;
        }
        
        a:hover {
            opacity: 1.0;
            filter: alpha(opacity=100);
        }
    }

    &__reload {
        display: none;
    }

    &__button {
        margin-right: 8px;
        height: 23px;
        font-size: 16px;
        font-family: @kievitregular;
        color: white;
        text-align: center;
        border-radius: 3px;
        cursor: pointer;
        display: table;   
        vertical-align: middle;    

        &__disabled {
            visibility: hidden;
            cursor: default;
        }

        &__account {
            padding-left: 15px;
            padding-right: 15px;
        }

        &__account.loggedin {
            background: #AAB8C2;
       }
    
        &__account.loggedin:hover {
            background: #7e93a7;
        }

        &__account.withdropdown {
            padding-right: 7px;
        }

        &__dropdown {
            width: 16px;
            height: 16px;
            padding-left:20px;
            display: table-cell;
            vertical-align: middle;
        }

        &__upgrade {
            padding-left: 14px;
            padding-right: 14px;

            &:focus {
                outline: none;
            }
            
            &__hide {
                visibility: hidden;
            }
        }

        &:focus {
            outline: none;
        }

        span {
            padding-top:1px;
            display: table-cell;
            vertical-align: middle;
        }

        i {
            font-size: 14px;
            display: table-cell;
            vertical-align: middle;
        }
    }

    &__buttonEnabled {
        &:hover {
            opacity: 1.0;
            filter: alpha(opacity=100);
            overflow: hidden;
            .transition(background-color 0.35s);
        }

        
        &__accountEnabled {
            background: #3AACFC;

            &:hover {
                background: #81D4FA;
            }            
        }
    }

    &__settings a {
        font-size: 15px;
        padding-right: 8px;
    }

    &__dropdown_menu {
        display: table-cell;
    }

    &__info {
        display: none;
    }

    &__close {
        font-size: 15px;
        padding-left: 8px;
    }
}

.header_menu {
    &_content {
        background: white;
        margin-top: 8px;
        position: absolute;
        border-style: solid;
        border-width: 1px;
        box-shadow: 1px; 
        display: block;
        border-radius: 3px;
        z-index: 2;

        .win-app & {
            right: 4px;
        }
    }

    &_content:hover {
        display: block;
        border-radius: 3px;
    }

    &_content:after {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        top: -8px;
        right: 10px;
        border-width: 0 8px 8px;
        border-color: transparent transparent #cfd6dc transparent;
        z-index: 9998;

        .win-app & {
            right: 18px;
        }
    }

    &_content:before {
        content: '';
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        top: -7px;
        right: 11px;
        border-width: 0 7px 7px 7px;
        z-index: 9999;

        .win-app & {
            right: 19px;
        }
   }

    &_content a {
        display: block;
        padding: 11px 10px 11px 10px;
        height: 34px;
        font-size: 14px;
        width: auto;
        opacity: 1.0;
        font-family: 'Kievit Light';
        color: black;
        text-align: left;
        margin: 1px;
        line-height: normal;
        border-radius: 3px;
    }

    &_content a:hover {
        color: black;
        background: #F3F5F6;
        border-radius: 3px;
    }
}

.header_menu_account {
    &_content {
        right: 47px;
    }
}

.header_menu_about {
    &_content {
        right: -10px;
    }
}
