@popupwhite: #F3F5F6;
@emphasisdark: #424242;

.ModalDataUsagePopup {
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 9999;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.4);
    -webkit-transition: 0.5s;
    overflow: auto;
    transition: all 0.3s linear;
}

.ModalDataUsagePopup-content {
    background-color: #FFFFFF;
    margin: auto;
    width: 320px;
    height: 456px;
    border-radius: 3px;
}


.is-hidden { display: none; }

.is-visuallyHidden {
    opacity: 0;
}

.is-blurred {
    filter: blur(2px);
    -webkit-filter: blur(2px);
}

.dataUsagePopup {
    display: table;
    text-align: center;
    width:100%;
    height: 100%;
    color :black;	
    font-size: 20px;	
    background-color: @popupwhite;
    border-radius: 3px;
    margin: 0 auto;
}

.img-stars {
    position: relative;
    top: -32px; 
    left: -23px;
    margin-right: -148px; 
}

.circle-progress {
    margin-top: 38px;
    transform: rotate(-90deg);
    background: @popupwhite;
    border-radius: 50%;
}

.progress-percentage-text {
    position:relative;
    top:-66px;
    right:-7px
}

.number-percentage {
    font-size: 30px;
    font-family: KievitCompPro-Light;
    color: #6F7073;
}

.sign-percentage {
    position: relative;
    left: -5px;
    font-size: 15px;
    font-family: KievitCompPro-Light;
    color: #6F7073;
}

.inner-circle {
    fill: @popupwhite;
    stroke: transparent;
}

.dataUsagePopup-title {
    margin-top: 20px;
    font-family: KievitCompPro-Light;
    color: @emphasisdark;
    font-size: 30px;
    line-height: 41px;
    height: 41px;
}

.dataUsagePopup-title .ng-scope {
    font-family: KievitCompPro-Light;
}

.line-separator {
    width:70px;
    height: 1px;
    margin:auto;
    vertical-align: middle;
    padding-top: 16px;
    border-bottom: 0.4px solid #CFD6DC;
}

.message.message-text.center-cell.noselect {
    font-size: 16px;
    font-family: KievitCompPro;
    color: #6F8299;
    vertical-align: top;
    padding-top:17px
}

.message.message-text.center-cell.noselect .ng-scope {
    font-family: KievitCompPro;
}

.message-text-split {
    padding-top: 4px;
}

.emphasis {
    color: @emphasisdark
}

.emphasis.ng-scope span {
    font-family: KievitCompPro
}

.message-cta {
    font-size: 18px;
    font-family: KievitCompPro;
    color: #6F8299;
    margin-top: 56px;    
}

.message-cta .ng-scope {
    font-family: KievitCompPro;
}

.message-not-now {
    cursor: pointer;
    width: 70px;
    margin: -3px auto 0px auto;
    padding-top: 0px;
    font-size: 16px;
    font-family: KievitCompPro;
    color: #6F8299
}

.message-text.message-not-now .ng-scope {
    font-family: KievitCompPro;
}

.upgrade-to-pro.round-button-circle.connect-button-circle.data-usage-button {
    margin-top:17px;
    width: 240px;
    background-color: #FFA400;
    height: 48px;
}

.upgrade-to-pro.round-button-circle.connect-button-circle .ng-scope {
    font-family: KievitCompPro;
    font-size: 20px;
    color: white
}

.cta {
    height: 18%;
    display: table;
    width: 100%;
    
    &__box {
        display: block;
    }

    .license-is-expiring & {
        height: @status-height-license-renew;
        padding-top: 4px;
    }

    .vpn-showRegisterOption & {
        margin-top: -5px;
        padding-top: 0;
        height: 89px;
    }

    .round-button-circle {        
        .vpn-disconnecting & {
            visibility: hidden;
        }
    }
}
