.features {
	display: table-row;
	width: 100%;

	&__page {
		height: 100%;
	}
	
	&__container {
		display: table;
		width: 100%;
		height: 100%;
		padding-right: 17px;
	}

	.about-linktext {
		top: @frame-height - 84px;
		width: @frame-width;
		margin-left: 10px;
		margin-right: 10px;
		text-align: center;
		.fs-18();
		line-height: 24px;
	}

	.quit-linktext {
		top: @frame-height - 36px;
		width: @frame-width;
		margin-left: 10px;
		margin-right: 10px;
		text-align: center;
		text-align: center;
		.fs-18();
		line-height: 24px;
	}

	.logout-linktext {
		top: @frame-height - 60px;
		width: @frame-width;
		margin-left: 10px;
		margin-right: 10px;
		text-align: center;
		text-align: center;
		.fs-18();
		line-height: 24px;
	}

	.quit-button-container {
		margin-top: 20px;
	}

	.quit-button {
		width: 200px;
	}
}
