*, *:after, *:before {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-family: @kievitlight;
}

.clearfix:after {
	visibility: hidden;
	display: block;
	content: "";
	clear: both;
	height: 0;
}

.waiting { cursor: wait; }

@import "_typography";

html {
	background: black;
}

.noOutline {
    outline:none;
}

[ng-cloak].splash {
    display:block !important;
    text-align:center;
}

.ng-cloak {
    display:none;
}

.splash {
    display:none;
}

body {
	background: white;
	width: 392px;
	height: 437px;
    border-radius: 4px;
	overflow: hidden;
	font-size: 0px; // avoid empty space after div (on Mac)
}

@show-easing: cubic-bezier(0.630, 0.895, 0.620, 0.990);

.prefix(@name, @rest...) {
	-webkit-@{name}: @rest;
	-ms-@{name}: @rest;
	-moz-@{name}: @rest;
	-o-@{name}: @rest;
	@{name}: @rest;
}

.transition(...) {
	-webkit-transition: @arguments;
	-moz-transition: @arguments;
	-o-transition: @arguments;
	-ms-transition: @arguments;
	transition: @arguments;
}

.box-shadow(...) {
	-webkit-box-shadow: @arguments;
	-moz-box-shadow: @arguments;
	-o-box-shadow: @arguments;
	-ms-box-shadow: @arguments;
	box-shadow: @arguments;
}

.border (@width: 1px; @style: solid; @color: @border-color) {
	border-color: @color;
	border-style: @style;
	border-width: @width;
}

.fadeIn (@duration: 1s) {
	-webkit-animation-name: fadeIn;
	animation-name: fadeIn;
	-webkit-animation-duration: @duration;
	animation-duration: @duration;
}

.slow {
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
}

