.settingsView-layout {
    width: 100%;
    height: 100%;
    text-align: center;
    display: table;

    &__features {
        display: table-row;
        height: 100%;

        &__middle {
            width: 90%;
            max-width: 535px;
            display: inline-block;
            height: 100%;
        }

        &__wide {
			width: 100%;
            max-width: 535px;
            display: inline-block;
            height: 100%;
        }
    }
}

.settings-switch {
    width: 100%;
    padding-left: 5%;
    padding-right:5%;    
    height: auto;
    min-height: 56px;
    border-bottom: 1px solid #D9D9D9;

    &__container {
        display: table;        
        min-height: inherit;
        width: 100%;                  
    }

    &__text {
        .fs-18();
        padding-left: 3px;
        padding-top: 10px;
        padding-bottom: 10px;
        vertical-align: middle;
        width: 80%;
        display: table-cell;
        text-align: left;

        &__title {
            padding-top: 5px;
            padding-bottom: 5px;
        }

        &__description {
            font-size: 14px;
            letter-spacing: 0.5px;
            line-height: 20px;
        }
    }

    &__button {
        height: 100%;
        vertical-align: middle;
        display: table-cell;
    }

    &__arrow {
        display:table-cell;
        height: 100%;
        width: 10%;
        vertical-align: middle;
    }

    &__pro {
        display: table;
        width: 100%; 

        &__text {
            display:table-cell;
            width: 90%; 
        }

        &__badge {
            display:table-cell;
            vertical-align: middle;
        }
    }

    &__beta {
        display: table;
        width: 100%; 

        &__text {
            display:table-cell;
            width: 90%; 
        }

        &__badge {
            display:table-cell;
            vertical-align: middle;
        }
    }

    &__image {
        float: right;
        vertical-align: middle;
    }
}

.wifi-switch {    
    display: table;
    height: 32px;
    width: 100%;

    &__box {
        display: table;
        vertical-align: middle;
        height: 100%;
        width: 100%;
    }

    &__text {
        display: table-cell;
        .fs-18();
        overflow: hidden;
        vertical-align: middle;
        text-align: left;
        width: 60%;
    }

    &__button {
        display: table-cell;
        vertical-align: middle;
    }

    &__logo {
        display: table-cell;
        height: 100%;
        float: left;
        vertical-align: middle;
    }
}

.regions-switch {
    margin-left: 5px;
    margin-right: 5px;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-right: 15px;    
    cursor:pointer;
    width: 100%;    
    display: table-cell;
    vertical-align: middle;
    min-height: inherit;
    display: table;
    
    &:hover {
        background-color: lighten(@border-color, 6%);
    }

    &__container {
        display: table-row;
        width:100%;
        min-height:inherit;
    }

    &__text {        
        .fs-18();
        padding-left: 3px;        
        overflow: hidden;
        display: table-cell;
        vertical-align: middle;
        height: 100%;
        word-wrap: break-word;
        text-align: left;
    }

    &__latency {
        font-family: @kievitregular;
        .fs-14();
        width: 70px;
        overflow: hidden;
        display: table-cell;
        vertical-align: middle;
        text-align: left;        
        min-height: inherit;
    }

    &__button {
        float: right;
        width: 15%;
        padding-top: 8px;
    }

    &__image {
        float: right;
        margin-top: 6px;
    }

    &__logo {
        width: 18px;
        height: 18px;
        float: left;
        margin-top: 2px;
        margin-right: 6px;
        padding-top: 14px;
    }
}

