.login {
    text-align: center;
    font-size: 18px;
    display: block;
    line-height: 1.3;
    overflow: hidden;
    user-select: none;
    width: 310px;
    height: 100%;
    display: inline-block;
}

.buttonBox {
    display: table-cell;
    vertical-align: middle;
}

.button {
    color: white;
    cursor: pointer;
    border: 1px solid;
    background-color: #23BE04;
    border-radius: 2px;
    font-size: 22px;
    padding: 14px;

    &__disabled {
        background-color: transparent;
        color: #7E93A7;
        border: 1px solid white;
        padding: 14px;
        font-size: 22px;
        border-radius: 2px;
        cursor: default;
    }
}

.button:hover {
    background-color: #25c804;
}

.button:active {
    background-color: #23be04;
}

.button:focus {
    outline: none;
}

.button--large {
    font-size: 22px;
    padding: 16px 100px;
}

.button--full {
    padding-left: 1rem;
    padding-right: 1rem;
    display: block;
    width: 100%;
}

.button--white {
    border: solid 1px #e6eaeb;
    background-color: white;
    color: #7e93a7;
}

.button--white:hover {
    background-color: #f4f6f6;
}

.input {
    font-size: 18px;
    border: solid 1px #CFD5E0;
    border-radius: 4px;
    background-color: #fcfcfc;
    padding: 0.75rem 1rem;
}

.toggle {
    font-size: 18px;
    display: table;
    height: 15%;
    vertical-align: middle;        
    width: 100%;
    
    &__box {
        display: table-cell;
        vertical-align: middle;        
    }

    &__link {
        color: #0059ef;
        cursor: pointer;
        display: inline-block;        
    }
}

.forgotPassword { 
    height: 19px;
    font-size: 14px;
    color: #6F8299;
    text-align: right;
    cursor: pointer;
    margin-top: 12px;
    margin-bottom: -6px;  
    float: right;
}

.registerEula {
    font-size: 14px;
    color: #6F8299;
    display: table-row;
    vertical-align: bottom;     
    height: 137px;
    
    &__text {
        display: inline-block;
        line-height: 19px;
        width: 90%;
        max-width: 535px;
    }
}

.input-group {
    height: 27%;
    display: table;
    width: 100%;

    &__box {
        display: table-cell;
        vertical-align: middle;
        width: 100%;
    }

    &__field {
        display: inline-block;
        width: 100%;
        outline: none;
        border: solid 1px #6B6D6F;
        border-radius: 2px;
        background-color: transparent;
        width: 100%;
        padding: 15px;
        font-size: inherit;
        transition: border-color 0.2s, background-color 0.2s;
        &:focus {
            border-color: #0083ff;
        }
        &:-webkit-autofill {
            box-shadow: 0 0 0px 1000px #fcfcfc inset;
            &:focus {
                box-shadow: 0 0 0px 1000px #f8fbff inset;
            }
        }
    }

    &__error {
        &__message {
            margin-top: 3px;
            min-height: 19px;
            font-size: 14px;
            color: #DE0B14;
            text-align: left;
        }

        .input-group__field {
            border-color: #e5281b;
            background-color: #fef8f8;
            &:focus {
                border-color: #e5281b;
                background-color: #fef8f8;
            }
        }
    }
}

.input::-webkit-input-placeholder {
    color: #b0c1d1;
}

.input::-moz-placeholder {
    color: #b0c1d1;
}

.verificationCode {
    font-size: 22px;
    color: #6F8299;
    display: table;
    height: 20%;

    &__text {
        display: table-cell;
        vertical-align: middle;
    }
}

.accountBlocked {
    display: table;
    height: 25%;
    width: 100%;

    &__box {
        display: table-cell;
        vertical-align: middle;
    }

    &__description {
        font-size: 22px;
        color: #6F8299;    
    }
}