.wifi {
	
	border-bottom: 1px solid @border-color;
	height: 67px;
	position:relative;
	background-color: white;

	&__left{
		float: left;
		text-align: left;
		position: relative;
		cursor: default;
		.prefix(pointer-events, all);
		height: 100%;
		width: 50px;
	}

	&__right {
		width: 70%;
		cursor: default;
	}

	&__header {
		width: 200px;
		line-height: 59px;
		padding-top: 3px;
		text-align: center;
		font-size: 25px;
	}

	&__setting--selected, &__setting--selected &__setting-icon {
		color: @color-enabled;
	}

	&__setting-icon {
		font-size: 20px;
		cursor: pointer;
        display: table-cell;
		vertical-align:middle;
		text-align: center;
		float: right;
    }
}

.ie8 .settings {
	&__setting--selected:after {
		left: 60px;
	}
}

.nowifitext {
	font-size: 17px;
	padding: 150px 20px;
	text-align: center;
}

.wifiNano {
	margin-left: 5px;
    margin-right: 5px;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-right: 15px;
    float: left;
	width: 100%;
	
	&:hover {
		background-color: lighten(@border-color, 6%);
		
		#wificlose {
			visibility:visible;
		}
    }
}

#wificlose {
	visibility:hidden;
	vertical-align: middle;
	display: table-cell;
	height: 100%;
	margin-right: 10px;
}

.wifiicon {
	display: table-cell;
	float: left;
	width: 20px;
	font-size: 25px;
	cursor:pointer;
	vertical-align: middle;
}

.wifiicon > small {
	height: 16px;
	width: 16px;
}